import { Grid, Paper, Button } from '@material-ui/core'
import TextField from '@material-ui/core/TextField';
import secureAxios from "../AxiosConfig/secureAxios";
import {connect} from 'react-redux'

import { Alert, AlertTitle } from '@material-ui/lab';
import {useState} from 'react'
import { Link } from 'react-router-dom';

const paperStyle = {
    padding: '20px',
    height: "50vh",
    width: 400,
    margin: '10px auto'

}

const btnstyle = {

    margin: '8px 0'
}



const ForgotPassword=(Props)=>{
    const{signinErrors, loginErrors}=Props
    const[passwordSuccess,setPassWordSuccess]=useState(false)

    const handleSubmit = (e) => {
        e.preventDefault()
        const credentials = JSON.stringify({
            email:e.target['email'].value,
            username: e.target['username'].value,
            newpassword: e.target['newpassword'].value
        });

        secureAxios({
            method: 'post',
            url: 'forgot_password/',
            headers: {

                'Content-Type': 'application/json'
            },
            data: credentials}).then(res => {
                    setPassWordSuccess(true)
                      
                        console.log(res.data)
                    }).catch(err => { signinErrors(err.response.data.message); console.log("error") })
            
            
}


    return(
        <div>

<form onSubmit={handleSubmit}>
        <Grid container > 
            
                <Paper elevation={10} style={paperStyle}>
                    {
                        !passwordSuccess && (<div>
                       
                    <Grid align='center'>
                       
                        <h2>Forgot Password</h2>
                    </Grid>
                    { loginErrors  && <Alert severity="warning"> {loginErrors}

                            — <strong>check it out!</strong> </Alert>}
        
                            
                    <TextField name='username' label="Username" placeholder="Enter Username" fullWidth required />
                    
                    <TextField fullWidth name='email' label='Email' placeholder="Enter your email" required />
                    { loginErrors && loginErrors.password && <Alert severity="warning"> {loginErrors.password[0]}

                            — <strong>check it out!</strong> </Alert>}


                    <TextField name='newpassword' label="Password" placeholder="Enter Password" type="password" fullWidth required />
            
                    <Button type='sumbmit' variant="contained" color="primary" style={btnstyle} fullWidth>Submit</Button>
                    </div> )}
            
                    { passwordSuccess && (<div>
                    <Alert severity="success">
                       <AlertTitle>Success</AlertTitle>
                     Password is set Suceessufully <strong>Please Login </strong>
                      </Alert>
                    <Link to='/login'> <Button variant="contained" color="primary" style={btnstyle} >Login</Button></Link>


                      </div>
                      
                    )}
                   
                </Paper>

            </Grid>
        </form>
     
       </div>

    )

}



const mapStatetoProps = state =>({
    loginErrors:state.loginErrors

})
const mapDispatchToProps = dispatch => ({
    // authSuccess: auth => dispatch({ type: 'AUTH_SUCCESS', payload: auth }),
    signinErrors:error=>dispatch({type:"SIGNIN_ERRORS",payload:error})

})
export default connect(mapStatetoProps, mapDispatchToProps)(ForgotPassword)