import {useSelector} from 'react-redux'

import {makeStyles } from "@material-ui/core/styles";
import Tooltip from "@material-ui/core/Tooltip";
import Zoom from "@material-ui/core/Zoom";

import { Avatar } from '@material-ui/core';
import {Link} from 'react-router-dom'
import PowerSettingsNewIcon from '@material-ui/icons/PowerSettingsNew';
const cssObj = {
  fontSize: "40px",
  backgroundColor: "grey",
  color: "white",
  padding: "3px",
  borderRadius: "5px",
  margin:"20px 18px",
  cursor: "pointer",
  boxShadow:"0 2px 2px 2px rgba(0, 0, 0, 0.2)"
};

const useStylesBootstrap = makeStyles((theme) => ({
  arrow: {
    color: theme.palette.common.black,
  },
  tooltip: {
    backgroundColor: theme.palette.common.black,
  },
  
}));
const style = {
  align:'center',
  height: '250px',
  width: '275px',
  backgroundColor: 'white',
  position: 'absolute',
  zIndex: '10',
  left: '82%',
  boxShadow:'0 2px 2px 2px rgba(0, 0, 0, 0.5)',
  display:'flex',
  borderRadius:'20px',
  right:'0px',
  top:'60px'

}
const formStyle={
  width: '80%',
  height: "80%",
  margin:"35px auto",
  /* background-color: violet; */
  textAlign: "center"

  
}
function BootstrapTooltip(props) {
  const classes = useStylesBootstrap();

  return (
    <Tooltip arrow classes={classes} TransitionComponent={Zoom} {...props} />
  );
}

function UserProfile(Props) {
  const state=useSelector(state=>state)
    const {authentication}=state


  
  return (
    <div style={style} >
      <div style={formStyle}>
        <Avatar >

      </Avatar>
        <h5>{`${authentication.first_name} ${authentication.last_name}`}</h5>
        {/* <h5>{authentication.username}</h5> */}
        <h5>{authentication.email}</h5>

        <BootstrapTooltip title="Logout">
        <Link to='/logout'>
            <PowerSettingsNewIcon style={cssObj}/> 
                </Link>
        </BootstrapTooltip>
        {/* <BootstrapTooltip title="Edit Info">
          <EditIcon style={cssObj} />
        </BootstrapTooltip> */}
       </div>
    </div>
  );
}


export default UserProfile