import React, { useState } from 'react';
import { useSelector } from 'react-redux'
import AddIcon from '@material-ui/icons/Add';
import CreateBlog from './CreateBlog';
import Popup from './Popup';
import { makeStyles, Toolbar, Button,Fab } from "@material-ui/core";
import CustomSnackbar from './CustomSnakbar';
import NavigationIcon from '@material-ui/icons/Navigation';


import Blog from './Blog';


const useStyles = makeStyles((theme) => ({

    newButton: {
        position: 'absolute',
        right: '10px',
        color: "blue",
        padding:'10px',
      
    },
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    }

}));


const BlogScreen = (Props) => {
    
    const [allBlog,setallBlog]=useState(true)
    const [Open, setOpen] = useState(false)
    const classes = useStyles();
    const [openSnack, setOpenSnack] = useState(false);



    return (
        <>
        <div className='container mt-3'>
            <div className="nav-scroller py-1 mb-2">
                <Toolbar>
                   <h5>Click Here For </h5> <Fab variant="extended" style={ { margin:'10px'}}> <NavigationIcon/><Button   onClick={()=>setallBlog(!allBlog)}>{allBlog?"MY BLOGS" : 'All BLOGS'} </Button> </Fab> 
                    {/* <Button  onClick={()=>setallBlog(false)}>MY BLOGS</Button> */}
                    

                    <Button
                        variant="outlined"
                        startIcon={ <AddIcon />}
                        className={classes.newButton}
                        onClick={() => setOpen(true)}
                    > New Blog</Button>
 
                      </Toolbar>
                     </div>
                   <Blog screenProps={Props} allBlog={allBlog}/>

                     </div>
                    <CustomSnackbar openSnack={openSnack}
                        message='Blog Created Succesfully'
                        setOpenSnack={setOpenSnack} />

                    <Popup
                        openPopup={Open}
                        setOpenPop={setOpen}

                        title="Create Blog">

                        <CreateBlog setOpenPop={setOpen} setOpenSnack={setOpenSnack} />
                    </Popup>
  
           
            
</>

    )

}



export default BlogScreen