
import { useState } from 'react'
import { Grid, Paper,  Typography, TextField, Button } from '@material-ui/core'
import { Alert} from '@material-ui/lab';
import secureAxios from "../AxiosConfig/secureAxios";

import { connect } from 'react-redux'
import {Link} from 'react-router-dom'

const paperStyle = { padding: '30px 20px', width: 500, margin: "20px auto", height: '80vh' }
const headerStyle = { margin: 0 }

const btnstyle = {

    margin: '15px 0'
}

const SignUp = (props) => {
    const { authSuccess, signinErrors, registerErrors,AddListOfBlogs} = props

    const [passwordCorrect, SetPassWordCorrect] = useState(true)

    const handleSubmit = (e) => {
        e.preventDefault()
        if (e.target['password2'].value == e.target['password'].value) {
            SetPassWordCorrect(true)

            var credentials = JSON.stringify({
                first_name: e.target['first_name'].value,
                last_name: e.target['last_name'].value,
                username: e.target['username'].value,
                email: e.target['email'].value,
                password: e.target['password'].value
            });
            console.log(credentials)
        } else {
            SetPassWordCorrect(false)
        }
        secureAxios({
            method: 'post',
            url: 'signUp/',
            headers: {
                'Content-Type': 'application/json'
            },
            data: credentials
        }).then(res => {
            authSuccess(res?.data);
            secureAxios({
                url:'blogs/',
                method:'get'
            }).then(response=>{
                AddListOfBlogs(response.data)
    
            }).catch(err=>console.log("Error"))
    
            props.history.push('/blogs')
            console.log(res?.data)
        }).catch(err => { signinErrors(err.response?.data); console.log("error") })



    }

    return (
        <div>
          
            <form onSubmit={handleSubmit}>
                <Grid container >

                    <Paper elevation={20} style={paperStyle}>
                        <Grid align='center'>
                            <h2 style={headerStyle}>Sign Up</h2>
                            <Typography variant='caption' gutterBottom>Please fill this form to create an account !</Typography>
                        </Grid>

                        <TextField fullWidth name='first_name' label='First Name' placeholder="Enter your First name" required />


                        <TextField fullWidth name='last_name' label=' Last Name' placeholder="Enter your Last name" required />

                        {registerErrors && registerErrors.email && <Alert severity="warning"> {registerErrors.email[0]}

                             — <strong>check it out!</strong> </Alert>}

                        <TextField fullWidth name='email' label='Email' placeholder="Enter your email" required />
                         { registerErrors && registerErrors.username && <Alert severity="warning"> {registerErrors.username[0]}

                            — <strong>check it out!</strong> </Alert>}

                        <TextField fullWidth name='username' label='Username' placeholder="Enter Username" required />
                        { registerErrors && registerErrors.password && <Alert severity="warning"> {registerErrors.password[0]}

                            — <strong>check it out!</strong> </Alert>}

                        <TextField fullWidth type='password' name='password' label='Password' placeholder="Enter your password" required />

                        <TextField fullWidth type='password' name='password2' label='Confirm Password' placeholder="Confirm your password" required />
                        {!passwordCorrect ? <Alert severity="warning">

                            Password and Confirmation Password does not Match! — <strong>check it out!</strong> </Alert> : <></>}

                        {/* <FormControlLabel
                        control={<Checkbox name="checkedA" />}
                        label="I accept the terms and conditions."
                    /> */}
                        <Button type='sumbmit' variant="contained" color="primary" style={btnstyle} fullWidth>
                            Sign Up</Button>

                            <Typography> 
                             Already have an Account?
                             <Link to='/login' >
                                Sign In
                                 </Link>
                        </Typography>
   
                    </Paper>

                </Grid>
            </form>
        </div>

    )
}

const mapStatetoProps = (state) => ({
    registerErrors: state.signUpErrors

})
const mapDispatchToProps = dispatch => ({
    authSuccess: auth => dispatch({ type: 'AUTH_SUCCESS', payload: auth }),
     signinErrors: error => dispatch({ type: "SIGNUP_ERROR", payload: error }),
     AddListOfBlogs:val=>dispatch({type:"ADD_LIST_BLOG",payload:val})

})

export default connect(mapStatetoProps, mapDispatchToProps)(SignUp)