const useStylesBootstrap = makeStyles((theme) => ({
  arrow: {
    color: theme.palette.common.black,
  },
  tooltip: {
    backgroundColor: theme.palette.common.black,
  },
}));

function BootstrapTooltip(props) {
  const classes = useStylesBootstrap();

  return <Tooltip arrow classes={classes} TransitionComponent={Zoom} {...props} />;
}




const Header = (Props) => {
  // const classes = useStyle()
  const state = useSelector(state => state)
  const dispatch = useDispatch()
  const { login } = state
  const [showProfile, setShowProfile] = useState(false)

  useEffect(() => {
    if (!login) {

      setShowProfile(false)

    }

  }, [login])

  return (


    <div>
      <nav className="navbar navbar-light navbar-expand-lg" style={{ backgroundColor: '#f2f2f2' }}>
        <Link className="navbar-brand" to='/'>Blog </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarTogglerDemo02">
          <ul className="navbar-nav mr-auto mt-2 mt-lg-0">
            <li className="nav-item active">
              <Link to='/'>
                <BootstrapTooltip title='Home'>
                  <HomeIcon style={{
                    color: "black",
                    fontSize: "40px",
                    //  marginLeft: "60",
                    cursor: "pointer"
                  }} />
                </BootstrapTooltip>
              </Link>
            </li>

          </ul>


          {login && (
            <div>
              <BootstrapTooltip title='User Profile'>
                <Avatar
                  style={{
                    color: "black",
                    fontSize: "40px",
                    cursor: "pointer"
                  }}
                  onClick={() => dispatch({ type: 'SHOW_PROFILE', payload: setShowProfile(!showProfile) })}

                />
              </BootstrapTooltip>


            </div>)}


          {!login && <Link to='/login'>
            <BootstrapTooltip title='Login'>
              <Button style={{
                color: "black",
                fontSize: "20px",

                cursor: "pointer"
              }}> Login </Button>
            </BootstrapTooltip>
          </Link>
          }

          {showProfile ? <UserProfile showProfile={showProfile} setShowProfile={setShowProfile} /> : <></>}

        </div>

      </nav>

    </div>






  )
}
export default Header


















