import {connect} from 'react-redux'
import { Link } from 'react-router-dom';
import {useEffect,useState} from 'react'
import localconfig from '../config';



const BlogDetail = (Props) => {

      const{ListOfBlogs}=Props
      const { id } = Props.match.params

    const[showBlog,setShowBlog]=useState({})

    useEffect(()=>{

        
        console.log(id)
        
    
        const blog=ListOfBlogs.find(item => {
       if (item.id == id) {
           
           return true
       }})
        // let blog=ListOfBlogs.find(item=>item.id=id)
        setShowBlog(blog)
       
    
    },[showBlog])
       

console.log(showBlog)        
    const capitalizeFirstLetter = (word) => {
        if (word)
            return word.charAt(0).toUpperCase() + word.slice(1);
        return '';
    };

    return (
        <>
        {Object.keys(showBlog).length > 0 &&
        <div className='container mt-3'>
           
            
             <h1 className='display-2'>{   showBlog?.title}</h1>
            <h2 className='text-muted mt-3'>Category: {capitalizeFirstLetter(showBlog && showBlog?.category)}</h2>
            <h4>{showBlog && showBlog?.month} {showBlog && showBlog?.day}</h4>
                    
            <div className='row'>
            <div className='col-sm'>
                     <img src={`${localconfig.host}${showBlog?.thumbnail}`} alt="Not Found" width="450" height="300"/>
                  </div>   
                  <div className='col-sm'>
                     <p>{ showBlog && showBlog?.content}</p>
                     </div>

                    
                     </div>
                     <hr />
                     <p className='lead mb-5'><Link to='/blogs' className='font-weight-bold'>Back to Blogs</Link></p>
                 
                   </div>
        }
         

        </>
    )
}
const mapStateToProps=state=>({ListOfBlogs:state.ListOfBlogs})
export default connect(mapStateToProps)(BlogDetail)



