import React from 'react'
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import Home from './components/Home';
import SignIn from '../src/accessControl/SignIn'
import SignUp from '../src/accessControl/SignUp'
import ForgotPassword from '../src/accessControl/ForgotPassword'
import {connect} from 'react-redux'
import {useEffect} from 'react'
import secureAxios from './AxiosConfig/secureAxios'
import BlogDetail from './components/BlogDetail';
import BlogScreen from './components/BlogScreen'
import Header from './components/Header';
import Logout from './accessControl/Logout';




function App(Props) {
    const{authInitiate,AddListOfBlogs}=Props
    useEffect(()=>{
        if(localStorage.getItem('username')){
        authInitiate()
        secureAxios({
            url:'blogs/',
            method:'get'
        }).then(response=>{
            AddListOfBlogs(response.data)

        }).catch(err=>console.log("Error"))

    }


    },[])

    return (
        <div >
           
  

        <Router>
            
        <Header/>
                <Switch>
                    <Route exact path='/' component={Home} />
                    <Route exact path='/blogs' component={BlogScreen} />
                    <Route exact path='/login' component={SignIn} />
                    <Route exact path='/logout' component={Logout} />
                    <Route exact path='/register' component={SignUp} />
                    <Route exact path='/forgotpassword' component={ForgotPassword} />
                    <Route exact path='/logout' component={Logout} />
                    <Route exact path='/blog/:id' component={BlogDetail} />
     

                </Switch>
         
        </Router>
       
        </div>
    );



    

}


const mapStateToProps=state=>({ListOfBlogs:state.ListOfBlogs }) 
const mapDispatchToProps=dispatch=>({
    authInitiate:()=>dispatch({type:'AUTH_INITIATE'}),
    AddListOfBlogs:val=>dispatch({type:"ADD_LIST_BLOG",payload:val})


})


export default connect(mapStateToProps,mapDispatchToProps)(App);
