
import { Grid, Paper, Avatar, Button, Typography } from '@material-ui/core'


import LockIcon from '@material-ui/icons/Lock';
import TextField from '@material-ui/core/TextField';
import { Link } from 'react-router-dom'

import { connect } from 'react-redux'

import { Alert } from '@material-ui/lab';
import secureAxios from "../AxiosConfig/secureAxios";


const paperStyle = {
    padding: '20px',
    height: "60vh",
    width: 400,
    margin: '10px auto'

}
const avtarStyle = { background: 'green' }
const btnstyle = {

    margin: '8px 0'
}

const SignIn = (Props) => {


    
    const { authSuccess } = Props
    const { signinErrors, loginErrors, AddListOfBlogs } = Props


    const handleSubmit = (e) => {
        e.preventDefault()
        const credentials = JSON.stringify({
            username: e.target['username'].value,
            password: e.target['password'].value
        });

        secureAxios({
            method: 'post',
            url: 'signIn/',
            headers: {
                'Content-Type': 'application/json'
            },
            data: credentials
        }).then(Response => {
            authSuccess(Response?.data)

            secureAxios({
                url: 'blogs/',
                method: 'get'
            }).then(response => {
                AddListOfBlogs(response.data)

            }).catch(err => console.log("Error"))

            Props.history.push('/blogs');
            console.log(Response?.data)
        }).catch(err => { signinErrors(err.response?.data.message); console.log('Error') })
        console.log(credentials)
    }


    return (
        <div className="sign">


            <form onSubmit={handleSubmit}>
                <Grid container >

                    <Paper elevation={10} style={paperStyle}>
                        <Grid align='center'>
                            <Avatar style={avtarStyle}>
                                <LockIcon />
                            </Avatar>
                            <h2>Sign In</h2>
                        </Grid>

                        {loginErrors && <Alert severity="warning"> {loginErrors}

                            — <strong>check it out!</strong> </Alert>}

                        <TextField name='username' label="Username" placeholder="Enter Username" fullWidth required />
                        {loginErrors && loginErrors.password && <Alert severity="warning"> {loginErrors.password[0]}

                            — <strong>check it out!</strong> </Alert>}

                            <TextField fullWidth type='password' name='password' label='Password' placeholder="Enter your password" required />

                        <Button type='sumbmit' variant="contained" color="primary" style={btnstyle} fullWidth>Sign In</Button>
                        <Typography>
                            <Link to='/forgotpassword' > Forgot Password </Link>
                        </Typography>
                        <Typography>
                            Do you have an Account?
                             <Link to='/register' >
                                Sign Up
  </Link>
                        </Typography>
                    </Paper>

                </Grid>
            </form>

        </div>
    )

}

const mapStatetoProps = state => ({
    loginErrors: state.loginErrors

})
const mapDispatchToProps = dispatch => ({
    authSuccess: auth => dispatch({ type: 'AUTH_SUCCESS', payload: auth }),
    signinErrors: error => dispatch({ type: "SIGNIN_ERRORS", payload: error }),
    AddListOfBlogs: val => dispatch({ type: "ADD_LIST_BLOG", payload: val })

})
export default connect(mapStatetoProps, mapDispatchToProps)(SignIn)
